body {
  background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
  color: #fff;
  font-family: 'Segoe UI', sans-serif;
  margin: 0;
  padding: 0;
}

.contact-container {
  max-width: 1100px;
  margin: 40px auto;
  padding: 30px;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.4);
}

.contact-title {
  text-align: center;
  font-size: 36px;
  margin-bottom: 20px;
  border-bottom: 2px solid #ffffff30;
  padding-bottom: 10px;
}

.contact-description {
  text-align: center;
  font-size: 18px;
  margin-bottom: 40px;
  color: #d1ffe2;
}

.contact-box {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  justify-content: space-between;
}

.contact-form {
  flex: 1;
  min-width: 280px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.contact-form label {
  font-weight: bold;
  color: #d1ffe2;
}

.contact-form input,
.contact-form textarea {
  padding: 10px 14px;
  border-radius: 10px;
  border: none;
  background-color: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  font-size: 16px;
  outline: none;
}

.contact-form textarea {
  resize: vertical;
}

.submit-btn {
  background-color: #2ecc71;
  color: white;
  border: none;
  padding: 12px;
  border-radius: 10px;
  font-size: 16px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.submit-btn:hover {
  background-color: #27ae60;
}

.contact-info {
  flex: 1;
  min-width: 280px;
}

.contact-info h3 {
  font-size: 24px;
  margin-bottom: 15px;
}

.contact-info p {
  font-size: 16px;
  margin: 8px 0;
  color: #d1ffe2;
}

.social-links a {
  display: block;
  margin-top: 8px;
  color: #00e6e6;
  text-decoration: none;
}

/* 🔁 Responsive Styles */
@media (max-width: 768px) {
  .contact-container {
    padding: 20px;
  }

  .contact-title {
    font-size: 28px;
  }

  .contact-description {
    font-size: 16px;
  }

  .contact-box {
    flex-direction: column;
    gap: 30px;
  }

  .contact-form input,
  .contact-form textarea {
    font-size: 15px;
  }

  .submit-btn {
    font-size: 15px;
    padding: 10px;
  }
}

@media (max-width: 480px) {
  .contact-title {
    font-size: 24px;
  }

  .contact-description {
    font-size: 15px;
  }

  .contact-form label {
    font-size: 14px;
  }

  .contact-info h3 {
    font-size: 20px;
  }

  .contact-info p {
    font-size: 14px;
  }

  .social-links a {
    font-size: 14px;
  }
}
